import React from 'react';
import {connect} from 'react-redux';

import {closePopout, goBack, openModal, openPopout, setPage} from '../../store/router/actions';
import * as VK from '../../services/VK';
import {Div, Panel, Alert, Group, Button, PanelHeader} from "@vkontakte/vkui";


class HomePanelBase extends React.Component {

    state = {
        showImg: false,
        firstName: "",
        lastName: "",
        errorGetAuthToken: false
    };

    componentDidMount() {
        if (this.props.accessToken === undefined) {
            this.getAuthToken();
        } else {
            this.getUserName();
        }
        // let t='[{"first_name":"Sergey","id":121212,"last_name":"Noskov"}]'; 
        // let js=JSON.parse(t); 
        // this.setState( 
        //     { 
        //     firstName: js[0].first_name, 
        //     lastName: js[0].last_name 
        //     });

        
        // if (this.props.accessToken === undefined) {
        //     this.getAuthToken();
        // } else {
        //     this.getUserName();
        // }
        // this.setState({
        //     firstName: "FirstName2",
        //     lastName: "LastName2"
        // });
    }

    getAuthToken() {
        this.props.dispatch(VK.getAuthToken(['groups','photos']));
    }

    componentDidUpdate(prevProps, prevState, snapshot) {
        if (this.props !== prevProps) {
            if (this.props.accessToken === null) {
                this.setState({
                    errorGetAuthToken: true
                });
            } else {
                this.setState({
                    errorGetAuthToken: false
                });
                this.getUserName();
            }
        }
    }

    showImg = () => {
        this.setState({showImg: true});
    };

    async getUserName() {
        if (localStorage.getItem('userData')) {
            let json = JSON.parse(localStorage.getItem('userData'));
            this.setState({
                firstName: json[0].first_name,
                lastName: json[0].last_name
            });
        
            return;
        }
        let userData = await VK.usersGet();

        localStorage.setItem('userData', JSON.stringify(userData));
        let userVK = JSON.parse(userData);
        this.setState({
            firstName: userVK[0].first_name, 
            lastName: userVK[0].last_name 
        });
    }

    openPopout() {
        this.props.openPopout(
            <Alert
                actions={[{
                    title: 'Нет',
                    autoclose: true,
                    style: 'cancel',
                }, {
                    title: 'Да',
                    autoclose: true,
                    action: this.showImg
                }]}
                onClose={() => this.props.closePopout()}
            >
                <h2>Вопрос значит</h2>
                <p>Вас роняли в детстве?</p>
            </Alert>
        );
    }

    render() {
        const {id, setPage, withoutEpic} = this.props;
        let temp = localStorage.getItem('userData');
        //let js = JSON.parse(temp);
        //let nameU = js[0].first_name;

        return (
            <Panel id={id}>
                <PanelHeader>Examples Sergey 15.03.21 25</PanelHeader>
                {this.state.errorGetAuthToken && <Group>
                    <Div>Возникла ошибка при получении данных, возможно Вы не выдали права на список групп.</Div>
                    <Div>
                        <Button size="l" stretched={true} onClick={() => this.getAuthToken()}>Запросить
                            повторно</Button>
                    </Div>
                </Group>}
                <Group>
                <Div>FirstName: {this.state.firstName}, lastName: {this.state.lastName}.</Div>
                <Div>userData:{temp}</Div>
                {/* <Div>first_name:{nameU}.</Div> */}
                    <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => setPage('home', 'groups')}>Список моих
                            групп</Button>
                    </Div>
                    <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => this.openPopout()}>Открыть алерт</Button>
                    </Div>
                    <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => this.props.openModal("MODAL_PAGE_BOTS_LIST")}>Открыть
                            модальную страницу</Button>
                    </Div>
                    {withoutEpic && <Div>
                        <Button mode="secondary" size="l" stretched={true} onClick={() => setPage('modal', 'filters')}>Открыть модальное окно</Button>
                    </Div>}
                    {this.state.showImg && <Div className="div-center">
                        <img src="https://vk.com/sticker/1-12676-256" alt="Стикер VK"/>
                    </Div>}
                </Group>
            </Panel>
        );
    }

}

const mapDispatchToProps = {
    setPage,
    goBack,
    openPopout,
    closePopout,
    openModal
};

export default connect(null, mapDispatchToProps)(HomePanelBase);
